import React, { Fragment, useEffect } from "react";
import { Table, Input } from "antd";
import { useSelector, useDispatch } from "react-redux";
import { QuanLyNguoiDungReducer } from "./../../../redux/reducers/QuanLyNguoiDungReducer";
import {
  layDanhSachNguoiDungAction,
  layThongTinNguoiDungAction,
} from "./../../../redux/actions/QuanLyNguoiDungAction";
import { NavLink } from "react-router-dom";
import {
  EditOutlined,
  DeleteOutlined,
  ContactsOutlined,
} from "@ant-design/icons";
export default function User() {
  const { listUser } = useSelector((state) => state.QuanLyNguoiDungReducer);
  // console.log(listUser);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(layDanhSachNguoiDungAction());
  }, []);

  const columns = [
    {
      title: "STT",
      dataIndex: "stt",
      width: 50,
    },
    {
      title: "Tn ti khon",
      dataIndex: "userName",
      width: 100,
    },
    {
      title: "H v tn",
      dataIndex: "name",
      width: 150,
    },
    {
      title: "Email",
      dataIndex: "email",
      width: 200,
    },
    {
      title: "S in thoi",
      dataIndex: "phoneNumber",
      width: 100,
    },
    {
      title: "Mt khu",
      dataIndex: "password",
      width: 100,
    },
    {
      title: "Loi ngi dng",
      dataIndex: "loaiND",
      width: 100,
    },
    {
      title: "",
      dataIndex: "action",
      width: 150,
    },
  ];
  const data = [];
  for (let i = 0; i < listUser.length; i++) {
    data.push({
      key: i,
      stt: i + 1,
      userName: listUser[i].taiKhoan,
      name: listUser[i].hoTen,
      email: listUser[i].email,
      phoneNumber: listUser[i].soDt,
      password: listUser[i].matKhau,
      loaiND: listUser[i].maLoaiNguoiDung,
      action: (
        <Fragment>
          <NavLink to="" className="text-green-700 text-2xl mr-5" key={1}>
            <ContactsOutlined />
          </NavLink>
          <NavLink to="" className="text-green-700 text-2xl mr-5" key={1}>
            <EditOutlined />
          </NavLink>
          <span
            className="text-red-700 text-2xl cursor-pointer mr-5"
            to="/"
            key={2}
            // onClick={() => {
            //   if (window.confirm("Bn c mun xa phim " + film.tenPhim)) {
            //     // Gi action
            //     dispatch(xoaPhimAction(film.maPhim));
            //   }
            // }}
          >
            <DeleteOutlined />
          </span>
        </Fragment>
      ),
    });
  }

  const { Search } = Input;
  const onSearch = (value) => console.log(value);
  return (
    <Fragment>
      <Search
        placeholder="Nhp thng tin cn tm"
        onSearch={onSearch}
        enterButton
      />
      <Table
        columns={columns}
        dataSource={data}
        pagination={{ pageSize: 10 }}
        scroll={{ y: "50vh" }}
      />
    </Fragment>
  );
}
